  exports["default"] = Ember.HTMLBars.template({ "id": "kOmQjyK6", "block": "{\"statements\":[[\"open-element\",\"h2\",[]],[\"flush-element\"],[\"text\",\"Jason Berlin\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"h3\",[]],[\"flush-element\"],[\"text\",\"Biograhy\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Jason Berlin received his BFA from Oregon Collage of Art and Craft and his MFA in Visual Studies at Hallie Ford School of Graduate Studies at Pacific Northwest College of Art. (Portland OR ) Berlin works  in the medium of oil painting, sculpture, installation, college, and curation. In 2016 Berlin started Stall Gallery as a curatorial project and in 2016 was awarded six month artist in residency at Rainmaker artist residency in Portland, Oregon. Berlin’s work has been exhibited in numerous art shows in the Pacific Northwest, and in group exhibitions in New York, France, and Vancouver BC. Berlin splits his time between the rural country side of Longview Washington ( his home town ) and urban life where he lives in Portland Oregon.\"],[\"close-element\"],[\"text\",\"\\n\\n\\n\"],[\"open-element\",\"p\",[]],[\"flush-element\"],[\"text\",\"Website: \"],[\"open-element\",\"a\",[]],[\"static-attr\",\"href\",\"http://jasonberlinthomas.com\"],[\"flush-element\"],[\"text\",\"Jason Berlin\"],[\"close-element\"],[\"close-element\"],[\"text\",\"\\n\\n\"],[\"append\",[\"unknown\",[\"artist-page-nav\"]],false],[\"text\",\"\\n\"],[\"append\",[\"unknown\",[\"outlet\"]],false],[\"text\",\"\\n\"]],\"locals\":[],\"named\":[],\"yields\":[],\"blocks\":[],\"hasPartials\":false}", "meta": { "moduleName": "verge-ember/templates/berlin.hbs" } });
